import { BrowserRouter, Route, Switch } from 'react-router-dom';

import SignIn from './signIn/signIn';
import ResetPassword from './resetPassword/resetPassword';
import ResetPasswordInput from './resetPasswordInput/resetPasswordInput';
import ResetPasswordConfirmation from './resetPasswordConfirmation/resetPasswordConfirmation';
import { GlobalStyle } from './Styled/Global';

function App() {
  return (
    <div className="App">
      <GlobalStyle />
      <BrowserRouter>
        <Switch>
          <Route path="/ironnet/">
            <SignIn />
          </Route>
          <Route path="/ironnet/resetpassword">
            <ResetPassword />
          </Route>
          <Route path="/ironnet/resetpasswordinput">
            <ResetPasswordInput />
          </Route>
          <Route path="/ironnet/resetpasswordconfirmation">
            <ResetPasswordConfirmation />
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
